import React, {useEffect, useState} from 'react';
import TextEditor from './TextEditor';
import Features from './Features';

function App() {
  const [html , setHtml] =useState('\n\n<!-- Enter Your HTML here -->\n\n<h1>No need of body head and html tags just start...</h1>');
  const [css , setCss] = useState("\n\n/* Enter your CSS code here */\n\nbody{\n\tbackground:cyan;\n\tcolor:purple;\n}");
  const [js , setJs] = useState('\n\n//  Enter Your JavaScript Code here');
  const [srcDoc, setSrcDoc] = useState('');
  const [theme, setTheme] = useState('default');
  const [frameWork, setFrameWork] = useState('');
  useEffect(() => {
    const timeout = setTimeout(() => {
      setSrcDoc(`
        <html>
	<head><meta name="viewport" content="width=device-width, initial-scale=1">
		${frameWork}
	</head>
        <body>${html}<body>
	<style>${css}</style>
	<script>${js}</script>
	</html>
	`) }, 600)

	return () => clearTimeout(timeout)
  }, [html, css, js]);
  return (
    <div className="App">
      <div className="half top">
	<TextEditor
		theme={theme}
		language='xml'
		displayName='HTML'
		value={html}
		onChange={setHtml}
	/>
	<TextEditor
		theme={theme}
		language='css'
		displayName='CSS'
		value={css}
		onChange={setCss}
	/>
	<TextEditor
		theme={theme}
		language='javascript'
	 	displayName='JS'
	 	value={js}
	 	onChange={setJs}
	/>
     </div>
     <div className="half">
	<iframe
	className="output-window"
	srcDoc={srcDoc}
	title="output"
	sandbox="allow-scripts"
	frameBorder="0"
	width="100%"
	height="100%"
		/>
	<div className="customize">
	    <Features onThemeChange={setTheme} onFrameworkSelect={setFrameWork}/>
	</div>
     </div>
    </div>
  );
}

export default App;
